import { styled } from '@mui/material/styles';
// material-ui
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, tableCellClasses, Typography, Grid } from '@mui/material';

// project imports
import MainCard from 'ui-component/cards/MainCard';
import SecondaryAction from 'ui-component/cards/CardSecondaryAction';
import { minWidth } from '@mui/system';

// table data
function createData(name, calories, fat, carbs, protein) {
    return { name, calories, fat, carbs, protein };
}

const DivValue = styled('div')(({ theme }) => ({
    ...theme.typography.button,
    backgroundColor: theme.palette.background.paper,
    paddingRight: 12,
    paddingTop: 2,
    fontSize: 16,
}));
const DivTitle = styled('div')(({ theme }) => ({
    ...theme.typography.button,
    backgroundColor: theme.palette.background.paper,
    color: theme.palette.mode === 'dark' ? theme.palette.common.white : theme.palette.common.black,
    paddingTop: 2,
    paddingLeft: 12,
    fontSize: 16,
    fontWeight: 400
}));

const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white
    },
    [`&.${tableCellClasses.body}`]: {
        fontSize: 14
    }
}));

const StyledTableCellGroup = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white
    },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
    },
    // hide last border
    '&:last-of-type td, &:last-of-type th': {
        border: 0
    }
}));

const rows = [
    ["", "20", "4", "5", "M^3", "Profile Detail", 1],
    ["", "20", "4", "5", "M^3", "Profile Detail", 2],
    ["", "20", "4", "5", "M^3", "Profile Detail", 2],
];

const basicInfo = [
    ["date", "2023 / 11 / 02 - 10 : 20 PM"],
    ["Proforma Invioce Block Swaing", "15284"],
    ["Client", "Zackery"],
]



// ==============================|| TABLE - DENSE ||============================== //

export default function Review() {
    return (
        <>
            <Grid container alignItems="center" justifyContent="flex-end" spacing={2} pl={4} mb={4}>
                <Grid item xs={12}>
                    {basicInfo.map((bi) =>
                        <Grid
                            container
                            direction="row"
                            justifyContent="flex-start"
                            alignItems="flex-end"
                        >
                            <Grid item>
                                <DivTitle>{bi[0]}</DivTitle>
                            </Grid>
                            <Grid item pl={2}>
                                <DivValue>{bi[1]}</DivValue>
                            </Grid>
                        </Grid>
                    )}
                </Grid>
            </Grid>
            <TableContainer>
                <Table sx={{ minWidth: 320 }} aria-label="customized table">
                    <TableHead>
                        <TableRow>
                            <StyledTableCell align="center">Observations</StyledTableCell>
                            <StyledTableCell align="center">Total</StyledTableCell>
                            <StyledTableCell align="center">Category</StyledTableCell>
                            <StyledTableCell align="center">Qty</StyledTableCell>
                            <StyledTableCell align="center">Unit</StyledTableCell>
                            <StyledTableCell align="center">Profile</StyledTableCell>
                            <StyledTableCell align="center">Item</StyledTableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            <StyledTableRow hover key={row.name}>
                                {row.map((cell) => (
                                    <TableCell align='center'>{cell}</TableCell>
                                ))}
                            </StyledTableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
            <Grid container alignItems="center" justifyContent="flex-end" spacing={2} pt={2} pl={4}>
                <Grid item xs={12}>
                    <Grid
                        container
                        direction="row"
                        justifyContent="flex-start"
                        alignItems="flex-end"
                    >
                        <Grid item>
                            <DivTitle>Total : </DivTitle>
                        </Grid>
                        <Grid item pl={2}>
                            <DivValue>50</DivValue>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
            <Grid container pb={3}>
                <Grid item xs={12} ml={4}>
                    <DivTitle>Reviews : </DivTitle>
                </Grid>
                <Grid item xs={12}>
                    <Typography pl={6}>
                        the price of a ton or m3, and the treatment is aded to the price per square meter according to the customer’s choice <br />
                        Quantities can be increased and decreased according to the final measurement <br />
                        or granite is received by the customer or the customer’s representative from the factory and with his knowledge before transportation to the site <br />
                        Price include Vat  14%
                    </Typography>
                </Grid>
            </Grid>
        </>
    );
}
